let ua = navigator.userAgent.toLowerCase();
let isIOS = !!ua.match(/\(i[^;]+;( u;)? cpu.+mac os x/); // IOS
let isIPhone = ua.indexOf('iphone') > 0; // iPhone
let isIPad = ua.indexOf('ipad') > 0; // iPad
let isAndroid = ua.indexOf('android') > 0; // 
let isPC = !(isIOS || isAndroid); // PC
let isQYWX = ua.match(/wxwork/) !== null; // 

export default {
  isIOS,
  isIPhone,
  isIPad,
  isAndroid,
  isPC,
  isQYWX,
}


