// mixin
import saleCommon from '../mixins/saleCommon'
export default {
  name: 'help-Report',
  mixins : [saleCommon],
  data() {
    return {
      areaList: [],
      areaValue: [],
      bankList: [],
      bankValue: []
    };
  },
  methods: {
    //
    end() {
      if (!this.btnStatus) return;
      // 
      let tips = '';
      let tipArr = [
        { key:'areaValue', msg:'' },
        { key:'bankValue', msg:'' },
        { key:'helpCaptain', msg:'' },
        { key:'governor', msg:'' },
        { key:'member', msg:'' },
        { key:'Aclass', msg:'A' },
        { key:'Bclass', msg:'B' },
        { key:'arr', msg:'' },
      ];
      tipArr.every( item => {
          if(!this[item.key] && this[item.key] !== 0){
            tips = `${item.msg}`
            return;
          }else if(item.key === 'areaValue' && this[item.key].length === 0 || item.key === 'areaValue' && this[item.key].indexOf('') === 0 || item.key === 'bankValue' && this[item.key].length === 0){
            tips = `${item.msg}`;
            return;
          }else if(item.key === 'arr' && this[item.key].length){
            for(let inx in this[item.key]){
              if(!this[item.key][inx].value.length){
                tips = `${item.msg}`;
              }
              else if(this[item.key][inx].value[0] !== '' && !this[item.key][inx].qty && this[item.key][inx].qty !== 0){
                tips = '';
              }
            }
            return;
          }  
        return true;
      })
      if(tips){
        this.$vux.alert.show({
          content: tips
        })
        return;
      }
      //
      localStorage.setItem(
        "HELP_ZONE_INFO",
        JSON.stringify({
          bank: this.bankValue[0],
          areaValue: this.areaValue[0],
          captain: this.helpCaptain
        })
      );
      //
      let governor = this.governor,
          member = this.member,
          comments = this.comments,
          HELP_ZONE_INFO = JSON.parse(localStorage.getItem("HELP_ZONE_INFO")),
          ROSE_OPTION = JSON.parse(localStorage.getItem("ROSE_OPTION"));  //

      // 
      let jsonData = {
        listId: "4bda3e47-a088-4749-a988-ebb07cfb00e4",
        referenceId: this.guid(),
        baseinfoExt: {
          id: this.guid(),
          varchar1: ROSE_OPTION.dept,         //() ()
          varchar2: HELP_ZONE_INFO.captain,   //
          varchar3: HELP_ZONE_INFO.areaValue, //()
          varchar4: HELP_ZONE_INFO.bank,      //
          varchar5: ROSE_OPTION.groupName,    //()
          varchar6: "",                     //
          varchar7: governor,                 //
          varchar8: member,                   //
          varchar9: comments,                 //
          varchar10: ROSE_OPTION.region,     //()
          varchar11: ROSE_OPTION.bank,        //()
          varchar12: ROSE_OPTION.userCode     //
        },
        transDetailUncalc: [
          {
            id: this.guid(),
            transObjCode: "A", //
            containerCode: "A", //
            qty: "",
            amount: Number(this.Aclass), //
            fgCode: ""
          },
          {
            id: this.guid(),
            transObjCode: "B", //
            containerCode: "B", //
            qty: "",
            amount: Number(this.Bclass), //
            fgCode: ""
          }
        ],
        transCode: "XHXSDD"
      };
      // 
      for (let i = 0; i < this.arr.length; i++) {
        if (this.arr[i].value[0] != "" && this.arr[i].qty === "") {
          this.$vux.alert.show({
            title: "",
            content: ""
          });
          return;
        } else {
          jsonData.transDetailUncalc.push({
            id: this.guid(),
            transObjCode:this.arr[i].value[0] === "" ? "" : this.arr[i].value[0].split("_")[0], //
            containerCode: "", //
            qty: this.arr[i].value[0] === "" ? "" : this.arr[i].qty,
            taxAmount: this.arr[i].value[0] === "" ? "" : Number(this.arr[i].value[0].split("_")[2]),
            amount: this.arr[i].qty * this.arr[i].value[0].split('_')[3], //
            fgCode: ""
          });
        }
      }
      let totalInfo = {
        isMobile: true,
        conn: 20000,
        list: "trans_form_data",
        transCode: "XHXSDD",
        jsonData: JSON.stringify(jsonData)
      };
      this.totalInfo = totalInfo;
      // 
      localStorage.setItem(
        "saleReportInfo",
        JSON.stringify({
          saleReportRemark: totalInfo,
          time: new Date().getTime()
        })
      );
      // 
      localStorage.setItem(
        "help_saleReport",
        JSON.stringify({
          saleReportArr: this.arr,
          Aclass: this.Aclass,
          Bclass: this.Bclass,
          time: new Date().getTime()
        })
      );
      // 
      localStorage.setItem(
        "HELP_INFO_LIST",
        JSON.stringify({
          captain: this.helpCaptain,
          governor: this.governor,
          member: this.member
        })
      );
      this.$router.push({ path: "/count" });
    }
  },
  created(){
    this.getArea();
    this.getBank();
    let HELP_INFO_LIST = localStorage.getItem("HELP_INFO_LIST") || '';
    let help_saleReport = localStorage.getItem("help_saleReport") || '';
    let HELP_ZONE_INFO = localStorage.getItem("HELP_ZONE_INFO") || '';
    // 
    if (HELP_INFO_LIST) {
      this.helpCaptain = JSON.parse(HELP_INFO_LIST).captain;
      this.governor = JSON.parse( HELP_INFO_LIST).governor;
      this.member = JSON.parse(HELP_INFO_LIST).member;
    }
    if (help_saleReport) {
      this.arr = JSON.parse(help_saleReport).saleReportArr;
      this.Aclass = JSON.parse(help_saleReport).Aclass;
      this.Bclass = JSON.parse(help_saleReport).Bclass;
    }
    if (HELP_ZONE_INFO) {
      this.areaValue = [JSON.parse(HELP_ZONE_INFO).areaValue];
      this.bankValue = [JSON.parse(HELP_ZONE_INFO).bank];
    }
  
  },
  beforeRouteLeave(to, from, next) {
    if (this.arr[0].value.length == 0 || to.name == "Count") {
      next();
    } else {
      this.$vux.confirm.show({
        title: "",
        content: "",
        confirmText: "",
        cancelText: "",
        onCancel:() => {
          localStorage.removeItem("help_saleReport");
          localStorage.removeItem("HELP_INFO_LIST");
          next();
        },
        onConfirm:() => {
          //
          localStorage.setItem(
            "help_saleReport",
            JSON.stringify({
              saleReportArr: this.arr,
              Aclass: this.Aclass,
              Bclass: this.Bclass,
              captain: this.helpCaptain,
              time: new Date().getTime()
            })
          );
          //
          localStorage.setItem(
            "HELP_ZONE_INFO",
            JSON.stringify({
              bank: this.bankValue[0],
              areaValue: this.areaValue[0],
              captain: this.helpCaptain
            })
          );
          //
          localStorage.setItem(
            "HELP_INFO_LIST",
            JSON.stringify({
              captain: this.helpCaptain,
              governor: this.governor,
              member: this.member
            })
          );
          next();
        }
      });
    }
  }
};



