// mixin
import saleCommon from '../mixins/saleCommon'
export default {
  name: 'sale-Report',
  mixins: [saleCommon],
  methods: {
    // 
    goCount() {
      // 
      const ROSE_OPTION = JSON.parse(localStorage.getItem("ROSE_OPTION")) || '';
      // 20:00 
      if (!this.btnStatus) return;
      // 
      let { dept, region, bank, groupName, userCode } = ROSE_OPTION
      // 
      let captain = this.helpCaptain, governor = this.governor,
          member = this.member, comments = this.comments;
      // 
      let tips = '';
      let tipArr = [
          { key: 'helpCaptain', msg: ''},
          { key: 'governor', msg: ''},
          { key: 'member', msg: ''},
          { key: 'Aclass', msg: 'A'},
          { key: 'Bclass', msg: 'B'},
          { key: 'arr', msg: ''}
        ];
      tipArr.every( item => {
          if(!this[item.key] && this[item.key] !== 0){
            tips = `${item.msg}`
            return;
          }  
          else if(item.key === 'arr' && this[item.key].length){
            for(let inx in this[item.key]){
              if(!this[item.key][inx].value.length){
                tips = `${item.msg}`;
              }
              else if(this[item.key][inx].value[0] !== '' && !this[item.key][inx].qty && this[item.key][inx].qty !== 0){
                tips = '';
              }
            }
            return;
          }  
        return true;
      })
      if(tips){
        this.$vux.alert.show({
          content: tips
        })
        return;
      }
      // 
      let jsonData = {
        listId: "4bda3e47-a088-4749-a988-ebb07cfb00e4",
        referenceId: this.guid(),
        baseinfoExt: {
          id: this.guid(),
          varchar1: dept,
          varchar2: captain,
          varchar3: region,
          varchar4: bank,
          varchar5: groupName,
          varchar6: "",
          varchar7: governor,
          varchar8: member,
          varchar9: comments,
          varchar12:userCode, //
        },
        transDetailUncalc: [
          { id: this.guid(),
            transObjCode: "A", //
            containerCode: "A", //
            qty: "",
            amount: Number(this.Aclass), //
            fgCode: ""
          },
          { id: this.guid(),
            transObjCode: "B", //
            containerCode: "B", //
            qty: "",
            amount: Number(this.Bclass), //
            fgCode: ""
          }
        ],
        transCode: "XHXSDD"
      };

      // 
      for (let i = 0; i < this.arr.length; i++) {
        jsonData.transDetailUncalc.push({
          id: this.guid(),
          transObjCode: this.arr[i].value[0] === "" ? "" : this.arr[i].value[0].split("_")[0], //
          containerCode: "", //
          qty: this.arr[i].value[0] === "" ? "" : this.arr[i].qty,
          taxAmount: this.arr[i].value[0] === "" ? "" : Number(this.arr[i].value[0].split("_")[2]),
          amount: this.arr[i].qty * this.arr[i].value[0].split('_')[3], //
          fgCode: ""
        });        
      }

      let totalInfo = {
        isMobile: true,
        conn: 20000,
        list: "trans_form_data",
        transCode: "XHXSDD",
        jsonData: JSON.stringify(jsonData)
      };
      this.totalInfo = totalInfo;
      //  
      localStorage.setItem(
        "saleReportInfo",
        JSON.stringify({
          saleReportRemark: totalInfo,
          time: new Date().getTime()
        })
      );
      //   
      localStorage.setItem(
        "SALE_BASIC_INFO",
        JSON.stringify({
          member: member,
          governor: governor,
          captain: this.helpCaptain,
          time: new Date().getTime()
        })
      );
      //   
      localStorage.setItem(
        "SALE_FORM_INFO",
        JSON.stringify({
          member: member,
          governor: governor,
          Aclass: this.Aclass,
          Bclass: this.Bclass,
          saleReportArr: this.arr,
          captain: this.helpCaptain,
          time: new Date().getTime()
        })
      );

      this.$router.push({ path: "/count" });
    }
  },
  created(){
    //  
    const SALE_BASIC_INFO = JSON.parse(localStorage.getItem("SALE_BASIC_INFO")) || '';
    if(SALE_BASIC_INFO){
      this.member = SALE_BASIC_INFO.member;
      this.governor = SALE_BASIC_INFO.governor;
      this.helpCaptain = SALE_BASIC_INFO.captain;
    }
  },
  mounted() {
    // 
    const SALE_FORM_INFO = JSON.parse(localStorage.getItem("SALE_FORM_INFO")) || '';       
    //
    if (SALE_FORM_INFO) {
      this.Aclass = SALE_FORM_INFO.Aclass;
      this.Bclass = SALE_FORM_INFO.Bclass;
      this.member = SALE_FORM_INFO.member;
      this.governor = SALE_FORM_INFO.governor;
      this.arr = SALE_FORM_INFO.saleReportArr;
      this.helpCaptain = SALE_FORM_INFO.captain;
    }
  },
  beforeRouteLeave(to, from, next) {
    if (!this.arr[0].value.length || to.name === "Count") {
      next();
    }
    else {
      this.$vux.confirm.show({
        title: "",
        content: "",
        confirmText: "",
        cancelText: "",
        onCancel: () => {
          localStorage.removeItem("SALE_BASIC_INFO");
          next();
        },
        onConfirm: () => {
          localStorage.setItem(
            "SALE_FORM_INFO",
            JSON.stringify({
              member: this.member,  
              Aclass: this.Aclass,
              Bclass: this.Bclass,
              governor: this.governor,
              saleReportArr: this.arr,
              captain: this.helpCaptain,
              time: new Date().getTime()
            })
          );
          next();
        }
      });
    }
  }
};



